html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
.movieList .weui-media-box_appmsg .weui-media-box__hd {
  width: 4.33333333rem;
  height: 6.66666667rem;
}
.pl {
  float: left;
}
.pr {
  float: right;
}
.clearfix::before {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.hide {
  display: none;
}
html,
body,
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.router-view {
  overflow: hidden;
}
.overwrite-left {
  fill: #fff;
  position: relative;
  top: -0.53333333rem;
  left: -0.2rem;
}
.weui-cells__title.drawer-movie-search {
  font-size: 22px;
  margin: 30px 0;
}
.vux-drawer-content .weui-cells {
  margin: 0 0.33333333rem;
  border-radius: 0.13333333rem;
}
.drawer-movie-search-btn {
  margin-top: 10px;
}
.drawer-group-img {
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  bottom: 15px;
}
.drawer-group-img img {
  display: block;
  width: 100%;
}



